.text-center {
  text-align: center;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #3584A6;
  font-size: 1.8rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #3584A6;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

@media (min-width: 850px) {
  .container-barbershop {
    height: 100vh;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.container-barbershop .image {
  height: 30rem;
  background-color: #0084AA;
  background-image: url(../img/main-logo.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center 20%;
}
@media (min-width: 480px) {
  .container-barbershop .image {
    height: 50rem;
  }
}
@media (min-width: 850px) {
  .container-barbershop .image {
    height: auto;
  }
}

.app {
  width: 95%;
  margin: 0 auto;
  padding-top: 5rem;
}
.app .section {
  margin-top: 2.5rem;
  display: none;
}
.app .section h2 {
  font-size: 2.2rem;
  font-weight: 700;
}
.app .section:first-of-type {
  margin-bottom: 2.5rem;
}

.show-section {
  display: block !important;
}

.tabs {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 280px) {
  .tabs {
    display: block;
  }
}
.tabs button {
  width: 100%;
  margin: 2rem auto;
  background-color: #70b4d1;
  font-size: 1.2rem;
  font-weight: 700;
  padding: 1rem;
  color: #FFFFFF;
  border: none;
  text-transform: uppercase;
  border-radius: 2rem;
  outline: none;
}
@media (max-width: 280px) {
  .tabs button {
    margin: 1rem auto;
  }
}
.tabs button.current {
  background-color: #1c4759;
}
.tabs button:hover {
  cursor: pointer;
}

.services-list {
  margin-top: 3.5rem;
}
@media (min-width: 850px) {
  .services-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.services-list .service {
  margin-bottom: 2rem;
  background-color: #70b4d1;
  padding: 1.6666666667rem;
  border-radius: 2rem;
}
@media (min-width: 850px) {
  .services-list .service {
    margin-bottom: 0;
  }
}
.services-list .service p {
  color: #FFFFFF;
  margin: 0 0 1rem 0;
}
.services-list .service .price-service {
  margin: 0;
  font-size: 3rem;
  font-weight: 700;
}
.services-list .service.selected {
  background-color: #1c4759;
}

.pagination {
  display: flex;
  justify-content: space-between;
  padding: 2.5rem 0;
  margin-bottom: 3rem;
}
.pagination button {
  display: block;
  background-color: #3584A6;
  color: #FFFFFF;
  padding: 1.5rem 3rem;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
  border: none;
  border-radius: 3rem;
  outline: none;
}
@media (max-width: 280px) {
  .pagination button {
    font-size: 1rem;
  }
}
.pagination button.hide {
  visibility: hidden;
}
.pagination button:hover {
  cursor: pointer;
}

.form {
  width: 100%;
  margin-top: 3.5rem;
}

.field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
@media (max-width: 280px) {
  .field {
    display: block;
  }
}
.field label {
  flex-basis: 10rem;
  color: #3584A6;
}
@media (max-width: 280px) {
  .field label {
    display: block;
    margin-bottom: 1rem;
  }
}
.field input {
  flex: 1;
  padding: 1.25rem;
  border-radius: 2rem;
  border: 2px solid #e1e1e1;
  color: #3584A6;
  outline: none;
}
@media (max-width: 280px) {
  .field input {
    display: block;
    width: 100%;
  }
}

.alert {
  padding: 2.5rem;
  text-align: center;
  color: #FFFFFF;
  width: 100%;
  border-radius: 3rem;
  display: block;
}
.alert.error {
  background-color: #FF144C;
}

.invalidate-appointment {
  text-align: center;
  margin: 2.5rem;
  text-transform: uppercase;
}

.content-summary p {
  margin-bottom: 0.5rem;
}
.content-summary p span {
  font-weight: 700;
  font-size: 2.4rem;
}
.content-summary .summary-services {
  margin-top: 5rem;
}
.content-summary .summary-services h3 {
  margin: 2.5rem 0;
}
.content-summary .summary-services .container-service {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 2px solid #e1e1e1;
}
.content-summary .summary-services .container-service .price {
  font-size: 2.4rem;
  font-weight: 700;
}

.total {
  text-align: right;
}